<template>
  <ec-container height="100%" title="查询交易衔接量申请单" icon="onetomany" color-code="001">
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-panel-item>
            <ec-query-panel :defaultFold="true" @query="handleQuery" @reset="queryReset">
              <ec-query-item label="状态">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="queryItems.finishState"
                  @change="finishStateChange"
                >
                  <a-select-option v-for="item in finishStateListOptions" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="全部">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="queryItems.sendState"
                >
                  <a-select-option v-for="item in billStatesList" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="发送状态">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="queryItems.weightUnit"
                >
                  <a-select-option v-for="item in transStatesEnumList" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <template #inner>
                <ec-query-item label="单据号">
                  <a-input v-model="queryItems.busiLo" />
                </ec-query-item>
                <ec-query-item label="操作类型">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="queryItems.operateType"
                  >
                    <a-select-option v-for="item in operateTypeListForDaily" :key="item.id">
                      {{ item.label }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="业务类型">
                  <a-cascader
                    v-model="queryItems.apprTypeCode"
                    :options="apprTypeCodeList"
                    placeholder="请选择"
                  />
                </ec-query-item>
                <ec-query-item label="新烟/陈烟">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="queryItems.operateType"
                  >
                    <a-select-option v-for="item in newOrOldLeafList" :key="item.id">
                      {{ item.label }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="供方单位查询范围">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="queryItems.operateType"
                  >
                    <a-select-option v-for="item in orgCidSearchRangeList" :key="item.id">
                      {{ item.label }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="供方单位类型">
                  <ec-select
                    showSearch
                    allowClear
                    mode="multiple"
                    placeholder="请选择"
                    v-model="queryItems.supplyOrgTypes"
                  >
                    <a-select-option v-for="item in orgTypeList" :key="item.orgType">
                      {{ item.orgTypeName }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="供方单位">
                  <SelectPicker
                    title="选择单位"
                    :lazy="false"
                    :search="false"
                    v-model="queryItems.supplyOrgCode_temporary"
                    :fetchData="getfetchData"
                    @ok="handlepickerValue($event, 'supplyOrgCode')"
                  />
                </ec-query-item>
                <ec-query-item label="需方单位查询范围">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="queryItems.operateType"
                  >
                    <a-select-option v-for="item in orgCidSearchRangeList" :key="item.id">
                      {{ item.label }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="需方单位类型">
                  <ec-select
                    showSearch
                    allowClear
                    mode="multiple"
                    placeholder="请选择"
                    v-model="queryItems.demandOrgTypes"
                  >
                    <a-select-option v-for="item in orgTypeList" :key="item.orgType">
                      {{ item.orgTypeName }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="需方单位">
                  <SelectPicker
                    title="选择单位"
                    :lazy="false"
                    :search="false"
                    v-model="queryItems.supplyOrgCode_temporary"
                    :fetchData="getfetchData"
                    @ok="handlepickerValue($event, 'supplyOrgCode')"
                  />
                </ec-query-item>
                <ec-query-item label="是否为结转业务">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="queryItems.operateType"
                  >
                    <a-select-option v-for="item in yesOrNoList" :key="item.id">
                      {{ item.label }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="申请日期">
                  <a-range-picker v-model="applyTime" @change="applyTimeChange" />
                </ec-query-item>
                <ec-query-item label="完成日期">
                  <a-range-picker v-model="finishTime" @change="FinishTimeChange" />
                </ec-query-item>
                <ec-query-item label="业务年度">
                  <a-date-picker
                    v-model="queryItems.busiYear"
                    placeholder="请输入业务年度"
                    show-time
                    format="YYYY"
                    value-format="YYYY"
                  />
                </ec-query-item>
              </template>
            </ec-query-panel>
          </ec-panel-item>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-table
            ref="table"
            border
            show-overflow
            height="100%"
            :auto="false"
            align="center"
            header-align="center"
            :loading="loading"
            :data="tableData"
            @page-action="handlePagination"
            :pagination="pagination"
          >
            <ec-table-col title="序号" width="80" type="seq" align="center"></ec-table-col>
            <ec-table-col title="标题" #default="{ row }">
              {{ getCombination(row) }}
            </ec-table-col>
            <ec-table-col title="状态" field="billStates" align="center" #default="{ row }">{{
              getbillState(row.billStates)
            }}</ec-table-col>
            <ec-table-col title="申请日期" field="applyDate"></ec-table-col>
            <ec-table-col title="完成日期" field="finishTime" align="center"></ec-table-col>
            <ec-table-col title="发送状态" field="sendState" align="center" #default="{ row }">
              {{ row.sendState === '0' ? '未发送' : row.sendState === '1' ? '已发送' : '' }}
            </ec-table-col>
            <ec-table-col title="操作" #default="{ row }" align="center">
              <a-button type="link" @click="handlerOpenDetails(row)">详细</a-button>
            </ec-table-col>
          </ec-table>
        </ec-panel-body>
      </ec-panel>
      <AddModel
        ref="addModalRef"
        :operateTypeListForDaily="operateTypeListForDaily"
        :newOrOldLeafList="newOrOldLeafList"
        :orgTreeData="orgTreeData"
      />
    </ec-container-body>
  </ec-container>
</template>

<script>
import { SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList, cloneDeep } from '@euler-material/ecm-utils';
import AddModel from './AddModel.vue';

export default {
  name: 'dailyQuery',
  components: {
    AddModel,
    SelectPicker,
  },
  data() {
    return {
      // 初始基本类型
      loading: true,
      tableData: [],
      // options
      operateTypeList: [],
      weightUnitList: [],
      leafTypeCodeList: [],
      leafFormCodeList: [],
      transStatesList: [],
      // 控制状态 的状态
      finishStateListOptions: [
        { id: '', label: '全部', usedState: '1' },
        { id: '0', label: '进行中', usedState: '1' },
        { id: '1', label: '完成', usedState: '0' },
        { id: '2', label: '废止', usedState: '0' },
      ],
      // 发送状态
      transStatesEnumList: [
        { id: '0', label: '未发送', auditState: '0' },
        { id: '1', label: '已发送', auditState: '1' },
      ],
      // 操作类型
      operateTypeListForDaily: [
        { id: '1', label: '增量', usedState: '1' },
        { id: '2', label: '减量', usedState: '0' },
        { id: '3', label: '解除', usedState: '0' },
      ],
      // 业务类型
      apprTypeCodeList: [],
      apprTypeCodeListed: [],
      // 单位查询范围
      orgCidSearchRangeList: [
        { id: 'BJ', label: '本级', isSelected: true },
        { id: 'BJJXJ', label: '本级及下级' },
      ],
      // 烟叶
      newOrOldLeafList: [
        { id: '1', label: '新烟', sendState: '0' },
        { id: '2', label: '陈烟', sendState: '1' },
      ],
      // 是否结转
      yesOrNoList: [
        { id: '1', label: '是' },
        { id: '0', label: '否' },
      ],
      // 全部流转状态
      billStatesList: [],
      // 单位类型
      orgTypeList: [
        { orgType: 'TOBACCO', orgTypeName: '商业' },
        { orgType: 'INDUSTRY', orgTypeName: '工业' },
        { orgType: 'REDRIED', orgTypeName: '复烤企业' },
        { orgType: 'PIECE', orgTypeName: '薄片加工企业' },
        { orgType: 'CUTTER', orgTypeName: '烟丝企业' },
        { orgType: 'EXPORT', orgTypeName: '进出口企业（到口岸）' },
      ],
      // 时间
      applyTime: null,
      finishTime: null,
      // 查询数据
      queryItems: {
        finishState: '',
      },
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      // 组织树Provider
      orgTreeData: [],
    };
  },
  computed: {
    _api() {
      return this.$apis.apm.dailyQuery;
    },
    _apply() {
      return this.$apis.centre.apply;
    },
    orgLevelEk() {
      return this.$store.getters.getAppConfig.orgLevelEk;
    },
    bizCode() {
      return this.$store.getters.getAppConfig.bizCode;
    },
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.DAILY_YEAR;
    },
    // 全部的流转状态
    allBillStatesList() {
      return this.orgLevelEk === '1'
        ? [
            { id: '0', label: '删除中', usedState: '1' },
            { id: '1', label: '对方单位确认中', usedState: '0' },
            { id: '2', label: '省公司确认中', usedState: '0' },
            { id: '3', label: '经办人初审中', usedState: '0' },
            { id: '4', label: '处长审核中', usedState: '0' },
            { id: '5', label: '副总经理审核中', usedState: '0' },
            { id: '6', label: '总经理审核中', usedState: '0' },
            { id: 'gjj', label: '总公司审核同意', usedState: '1' },
            { id: 'prov', label: '省公司审核同意', usedState: '1' },
            { id: '9', label: '已删除', usedState: '0' },
            { id: '10', label: '废止', usedState: '0' },
          ]
        : [
            { id: '0', label: '删除中', usedState: '1' },
            { id: '1', label: '对方单位确认中', usedState: '0' },
            { id: '2', label: '省公司确认中', usedState: '0' },
            { id: '3,4,5,6', label: '总公司审核中', usedState: '0' },
            { id: 'gjj', label: '总公司审核同意', usedState: '1' },
            { id: 'prov', label: '省公司审核同意', usedState: '1' },
            { id: '9', label: '已删除', usedState: '0' },
            { id: '10', label: '废止', usedState: '0' },
          ];
    },
  },
  created() {
    this.init();
  },
  methods: {
    // 初始化
    async init() {
      this.handleQuery();
      this.getEnumerationdata();
      this.treeGetlist(this.bizYear, treeFormatList, this._apply);
    },
    // 请求分页查询
    async getTableData() {
      try {
        const parmas = { ...this.queryItems };
        parmas.busiYear = this.busiYear;
        if (!parmas.busiYear) return;
        const res = await this._api.query(parmas);
        this.tableData = res.rows;
        this.pagination.total = res.totalRows;
      } catch (err) {
        console.log(err);
      }
      this.loading = false;
    },
    // 分页
    handlePagination(pageNumber, pageSize) {
      this.queryItems.pageNumber = pageNumber;
      this.queryItems.pageSize = pageSize;
      this.handleQuery();
    },
    // 重置
    queryReset() {},
    // 按钮-详细
    handlerOpenDetails(row) {
      const params = { asDailyDealMasId: row.asDailyDealMasId, busiYear: this.busiYear };
      this._api.queryDetails(params).then((res) => {
        const apprTypeName = this.apprTypeCodeListed?.find(
          (item) => item.contTypeCode === res.apprTypeCode,
        )?.contTypeName;
        this.$refs.addModalRef.show({ ...res, apprTypeName });
      });
    },
    // 搜索按钮
    handleQuery() {
      this.getTableData();
    },
    // 动态改变 状态集合
    finishStateChange(val) {
      const isgjj = this.orgLevelEk === '1'; // 国家局
      // 选择进行中
      if (val === '0') {
        const isgjjfilter = isgjj
          ? (item) =>
              item.id === '0' ||
              item.id === '1' ||
              item.id === '2' ||
              item.id === '3' ||
              item.id === '4' ||
              item.id === '5' ||
              item.id === '6'
          : (item) =>
              item.id === '0' || item.id === '1' || item.id === '2' || item.id === '3,4,5,6';
        this.billStatesList = this.allBillStatesList.filter(isgjjfilter);
      } else if (val === '1') {
        // 选择完成
        const getfilterItem = (item) => item.id === 'gjj' || item.id === 'prov' || item.id === '9';
        this.billStatesList = this.allBillStatesList.filter(getfilterItem);
      } else if (val === '2') {
        // 选择终止
        this.billStatesList = this.allBillStatesList.filter((item) => item.id === '10');
      } else {
        this.billStatesList = this.allBillStatesList.map((item) => item);
      }
    },
    // 单位改变
    handlepickerValue() {},
    // 获取枚举
    getEnumerationdata() {
      this.billStatesList = [...this.finishStateListOptions];
      this.querygetBusinessType();
      // // 烟叶类型
      // this._api.queryDetailPcTAsImportAppr({}).then((res) => {
      //   debugger;
      // });
      this.$apis.enum.index
        .getEnumType({
          enumTypeCodes: ['OPERATE_TYPE', 'WEIGHT_UNIT', 'SEND_STATE', 'TRANS_STATES'],
        })
        .then((res) => {
          if (res) {
            this.operateTypeList = res.enumMap.OPERATE_TYPE;
            this.weightUnitList = res.enumMap.WEIGHT_UNIT;
            // this.leafTypeCodeList = res.enumMap.LEAF_TYPE_CD;
            // this.leafFormCodeList = res.enumMap.LEAF_FORM_CD;
            this.transStatesList = res.enumMap.TRANS_STATES;
          }
        });
    },
    // utils
    // 获取组织树
    async treeGetlist(yearValue, utils, api) {
      const treeParams = {
        bizYear: yearValue,
        bizCode: 'BA20111000001000000000000',
        orgTypeEk: 'EXPORT,INDUSTRY,TOBACCO',
        orgLevelEk: '3',
        paramMap: {
          isCorporation: '1',
        },
      };
      const fields = {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      };
      const rows = JSON.parse(localStorage.getItem('apprlist')) || [];
      if (rows.length) {
        this.orgTreeData = utils(rows, fields);
      } else {
        const response = await api.getreeDate(treeParams);
        this.orgTreeData = utils(response?.rows, fields);
      }
    },
    // 渲染组织树
    getfetchData() {
      return new Promise((resolve) => {
        const list = cloneDeep(this.orgTreeData);
        resolve(list);
      });
    },
    // 组合table标题字段
    getCombination(row) {
      const result = `${row.billNo || ''}-${this.getOperateType(row.operateType) || ''}-${
        row.supplyOrgName || ''
      }-${row.baseCellName || ''}-${row.demandOrgName || ''}`;
      return result;
    },
    // table单元格居右
    cellStyle({ $columnIndex }) {
      if ($columnIndex === 2 || $columnIndex === 1) {
        return { textAlign: 'left' };
      }
    },
    // 获取流转状态
    getbillState(id) {
      const list = [
        { id: '0', label: '删除中', usedState: '1' },
        { id: '1', label: '对方单位确认中', usedState: '0' },
        { id: '2', label: '省公司确认中', usedState: '0' },
        { id: '3', label: '经办人初审中', usedState: '0' },
        { id: '4', label: '处长审核中', usedState: '0' },
        { id: '5', label: '副总经理审核中', usedState: '0' },
        { id: '6', label: '总经理审核中', usedState: '0' },
        { id: 'gjj', label: '总公司审核同意', usedState: '1' },
        { id: 'prov', label: '省公司审核同意', usedState: '1' },
        { id: '9', label: '已删除', usedState: '0' },
        { id: '10', label: '废止', usedState: '0' },
      ];
      return list.find((item) => item.id === id)?.label || '';
    },
    // 操作类型
    getOperateType(id) {
      return this.operateTypeListForDaily?.find((item) => item.id === id)?.label || '';
    },
    querygetBusinessType() {
      // 业务类型
      this.$apis.common.index.queryContType({ contTypeCode: '2' }).then(({ rows }) => {
        this.apprTypeCodeListed = rows;
        this.apprTypeCodeList = this.getTree(
          rows,
          'higherContTypeCode',
          'contTypeCode',
          'contTypeName',
          'children',
          '0',
        );
      });
    },
    // 获取业务类型
    getTree(rows, pid, id, title, children, root) {
      // 递归 161202 次 5ms左右时间
      console.time();
      const list2tree1 = (list, topid) => {
        return list.filter((item) => {
          if (item[pid] === topid) {
            (item.value = item[id])((item.label = item[title]))(
              (item[children] = list2tree1(list, item[id])),
            );
            return true;
          }
          return false;
        });
      };
      const tree = list2tree1(rows, root);
      console.timeEnd();
      return tree;
    },
    // 时间
    applyTimeChange(time) {
      this.queryItems.beginApplyDate = time[0].format('YYYY-MM-DD HH:mm:ss') || '';
      this.queryItems.endApplyDate = time[0].format('YYYY-MM-DD HH:mm:ss') || '';
    },
    FinishTimeChange(time) {
      this.queryItems.beginFinishDate = time[0].format('YYYY-MM-DD HH:mm:ss') || '';
      this.queryItems.endFinishDate = time[0].format('YYYY-MM-DD HH:mm:ss') || '';
    },
  },
};
</script>

<style></style>
