<template>
  <ec-drawer title="新增" :visible="visible" size="large" @close="onclose" class="ec-draw-form">
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <div class="header-title">
          <div><span class="middle-title">主信息</span></div>
          <div>
            <span class="middle-title"
              >业务年度：{{ busiYear }} 计量单位：{{ unitstr }} 是否为转结业务：否</span
            >
          </div>
        </div>
        <ec-form ref="ruleForm" :model="ruleForm" :rules="rules">
          <a-row type="flex">
            <a-col :span="8">
              <a-form-model-item label="供方单位">
                <SelectPicker
                  title="选择单位"
                  :lazy="false"
                  :search="false"
                  v-model="ruleForm.supplyOrgCode"
                  :fetchData="getfetchData"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="需方单位">
                <SelectPicker
                  title="选择单位"
                  :lazy="false"
                  :search="false"
                  v-model="ruleForm.demandOrgCode"
                  :fetchData="getfetchData"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="申请日期">
                <a-date-picker
                  v-model="ruleForm.applyDate"
                  show-time
                  format="YYYY-MM-DD"
                  value-format="YYYY-MM-DD"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="操作类型">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="ruleForm.operateType"
                >
                  <a-select-option v-for="item in operateTypeListForDaily" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="业务类型">
                <a-input v-model="ruleForm.apprTypeName" autocomplete="off" />
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="陈烟/原烟">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="ruleForm.operateType"
                >
                  <a-select-option v-for="item in newOrOldLeafList" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </a-form-model-item>
            </a-col>
          </a-row>
        </ec-form>
        <a-divider />
        <a-row style="margin: 0 0 6px 0">
          <a-col :span="12"><span class="middle-title">明细信息</span></a-col>
          <a-col :span="12"><span class="middle-title">申请原因</span></a-col>
        </a-row>
        <a-row>
          <a-col :span="12">
            <vxe-table
              ref="xTable"
              height="300"
              border
              show-footer
              show-overflow
              highlight-hover-row
              header-align="center"
              align="center"
              class="editable-footer"
              :footer-method="footerMethod"
              :data="pcTAsDailyDealDetlDTOS"
            >
              <vxe-column width="60" type="seq" title="序号" align="center" />
              <vxe-column title="烟叶类型" field="leafTypeName"></vxe-column>
              <vxe-column title="烟叶形态" field="leafFormName"></vxe-column>
              <vxe-column title="衔接量" field="apprWeight"></vxe-column
            ></vxe-table>
          </a-col>
          <a-col :span="11" :push="1">
            <a-textarea
              style="border-radius: 16px"
              v-model="applyReasion"
              placeholder=" "
              :rows="14"
            />
          </a-col>
        </a-row>
        <!-- <ec-divider /> -->
        <a-row style="margin: 0 0 6px 0">
          <a-col :span="12"><span class="middle-title">审核记录</span></a-col>
        </a-row>
        <vxe-table
          border
          show-footer
          show-overflow
          highlight-hover-row
          ref="xTable"
          height="300"
          class="editable-footer"
          :data="pcTAsDailyDealAuditOpinionDTOS"
        >
          <vxe-column title="序号" width="60" type="seq" align="center" />
          <vxe-column title="操作人" field="auditorName"></vxe-column>
          <vxe-column title="意见" field="auditOpinion"></vxe-column>
          <vxe-column title="操作日期" field="auditTime"></vxe-column>
          <vxe-column title="操作" #default="{ row }" field="limitWeight">{{
            row.limitWeight
          }}</vxe-column></vxe-table
        >
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button @click="onclose">关闭</a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </ec-drawer>
</template>
<script>
import { SelectPicker } from '@euler-material/ecm-ui';
import { cloneDeep } from '@euler-material/ecm-utils';
import moment from 'moment';

export default {
  name: 'queryModel',
  components: { SelectPicker },
  props: {
    operateTypeListForDaily: {
      type: Array,
      default: () => [],
    },
    newOrOldLeafList: {
      type: Array,
      default: () => [],
    },
    orgTreeData: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      busiYear: null,
      unitstr: null,
      applyReasion: null,
      weightUnitName: '50',
      pcTAsDailyDealDetlDTOS: [],
      pcTAsDailyDealAuditOpinionDTOS: [],
      ruleForm: {},
      rules: {},
      visible: false,
      businessTypeOptions: [
        { label: '工商普通购销', value: 'HT1101' },
        { label: '出口备货普通购销', value: 'HT1201' },
        { label: '基地烟', value: 'HT1104' },
        { label: '国内合计', value: 'HT11' },
      ],
    };
  },
  methods: {
    moment,
    // 数据初始化
    // 显示推窗
    show(response) {
      this.busiYear = response.busiYear;
      this.unitstr = response.unitstr;
      this.applyReasion = response.applyReasion;

      this.ruleForm.demandOrgCode = response.demandOrgCode;
      this.ruleForm.supplyOrgCode = response.supplyOrgCode;
      this.ruleForm.applyDate = moment(new Date(response.applyDate));
      this.ruleForm.operateType = response.operateType;
      this.ruleForm.newOrOldLeaf = response.newOrOldLeaf;
      this.ruleForm.apprTypeName = response.apprTypeName;
      this.pcTAsDailyDealDetlDTOS = response.pcTAsDailyDealDetlDTOS;
      this.pcTAsDailyDealAuditOpinionDTOS = response.pcTAsDailyDealAuditOpinionDTOS;
      this.visible = true;
    },
    // 关闭
    onclose() {
      this.visible = false;
    },
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        count += Number(item[field]);
      });
      return count;
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return '合计';
          }
          if (['finalWeight', 'supplyWeight', 'demandWeight'].includes(column.property)) {
            return this.sumNum(data, column.property);
          }
          return null;
        }),
      ];
    },
    onGetapprTypeCode(val) {
      const list = [...this.businessTypeOptions];
      return list.find((item) => item.value === val)?.label;
    },
    // 单位换算
    handlerWeightcalculate(num, unit) {
      return num / unit;
    },
    // 渲染组织树
    getfetchData() {
      return new Promise((resolve) => {
        const list = cloneDeep(this.orgTreeData);
        resolve(list);
      });
    },
  },
};
</script>
<style lang="less" scoped>
/deep/.ant-modal-header {
  padding: 8px 24px;
}
/deep/.ant-modal-body {
  padding: 0;
}
.ec-container .ec-container-body {
  background-color: transparent;
}
.ant-descriptions:not(:last-child) {
  margin-bottom: 15px;
}
/deep/.ant-descriptions-title {
  margin-bottom: 20px;
  color: rgba(24, 144, 255, 1);
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
}
.vxetable-title {
  margin-bottom: 10px;
  color: rgba(24, 144, 255, 1);
  font-weight: bold;
  font-size: 16px;
  line-height: 2.4;
}
.header-title {
  margin-bottom: 8px;
  display: flex;
  justify-content: space-between;
}
.middle-title {
  color: blue;
}
</style>
